/*== Disabled Link ==*/
.u-disabled-link {
  text-decoration: none !important;
  cursor: text; }

/*== Lists ==*/
.u-bulleted-list {
  list-style: disc; }
  .u-bulleted-list li {
    margin-left: 1.5rem; }

/*== Card ==*/
.u-card {
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1), 0 8px 6px rgba(0, 0, 0, 0.02), 0 12px 6px rgba(0, 0, 0, 0.02), 0 16px 6px rgba(0, 0, 0, 0.02), 0 20px 6px rgba(0, 0, 0, 0.02); }

/*== Spacing ==*/
.u-spacing-top-half {
  margin-top: 0.375rem; }

.u-spacing-top-1x {
  margin-top: 0.75rem; }

.u-spacing-top-2x {
  margin-top: 1.5rem; }

.u-spacing-top-4x {
  margin-top: 3rem; }

.u-spacing-bottom-1x {
  margin-bottom: 0.75rem; }

.u-spacing-bottom-2x {
  margin-bottom: 1.5rem; }

.u-spacing-bottom-4x {
  margin-bottom: 3rem; }

/*== Padding ==*/
.u-padding-top-1x {
  padding-top: 0.75rem; }

.u-padding-top-2x {
  padding-top: 1.5rem; }

.u-padding-top-4x {
  padding-top: 3rem; }

.u-padding-bottom-1x {
  padding-bottom: 0.75rem; }

.u-padding-bottom-2x {
  padding-bottom: 1.5rem; }

.u-padding-bottom-4x {
  padding-bottom: 3rem; }

/*== Offscreen (hidden text for the sight impaired) ==*/
.u-offscreen {
  position: absolute;
  left: -9999px; }

/*== Clear Fix ==*/
.u-clear-fix:after {
  content: '';
  display: block;
  clear: both; }

/*== Clickable ==*/
.u-clickable {
  cursor: pointer; }

/*== Flash Message ==*/
.u-flash-message {
  background-color: #3C844B;
  color: #fff;
  padding: 0.75rem;
  font-size: 0.75rem; }
  @media (min-width: 600px) {
    .u-flash-message {
      text-align: center; } }

/*================================================
 Themes (t)
==================================================
 Sets background and text colors for an element
 and its descendants.
================================================*/
.t-dark {
  background-color: #3C844B;
  color: #fff; }
  .t-dark a,
  .t-dark h1,
  .t-dark h2,
  .t-dark h3,
  .t-dark h4,
  .t-dark h5,
  .t-dark h6 {
    color: #fff; }

.t-light {
  background-color: #fff; }

/*================================================
 Buttons (btn)
==================================================
 Global button styles.
================================================*/
/*== Buttons ==*/
.btn-primary,
.btn-secondary,
.btn-tertiary,
[type=submit] {
  display: inline-block;
  border: solid 0.1875rem #B22D26;
  padding: 0 0.75rem;
  border-radius: 4px;
  font-size: 1rem;
  line-height: 2;
  text-decoration: none;
  text-align: center;
  color: #fff;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none; }
  .btn-primary:focus,
  .btn-secondary:focus,
  .btn-tertiary:focus,
  [type=submit]:focus {
    outline: none; }
  input + .btn-primary,
  input + .btn-secondary,
  input + .btn-tertiary,
  input + [type=submit] {
    margin-top: 0.75rem; }
  .btn-primary[disabled],
  .btn-secondary[disabled],
  .btn-tertiary[disabled],
  [type=submit][disabled] {
    background-color: #a7a9ac;
    border-color: #a7a9ac;
    color: rgba(0, 0, 0, 0.5);
    cursor: default;
    text-shadow: none; }
    .btn-primary[disabled]:hover,
    .btn-secondary[disabled]:hover,
    .btn-tertiary[disabled]:hover,
    [type=submit][disabled]:hover {
      border-color: #a7a9ac; }

.btn-primary {
  background-color: #B22D26;
  border-color: #B22D26;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.3); }
  .t-dark .btn-primary:hover,
  .t-dark .btn-primary:focus {
    border-color: #dd6761; }
  .t-dark .btn-primary:focus {
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.3); }
  .btn-primary:hover,
  .btn-primary:focus {
    border-color: #8c231e; }
  .btn-primary:focus {
    box-shadow: 0 0 0 3px #B22D26; }

.btn-secondary {
  background-color: #3C844B;
  border-color: #3C844B; }
  .btn-secondary:hover,
  .btn-secondary:focus {
    border-color: #2e6439; }
  .btn-secondary:focus {
    box-shadow: 0 0 0 3px #3C844B; }

.btn-tertiary {
  background-color: transparent;
  color: #a7a9ac;
  border-color: rgba(255, 255, 255, 0.1); }
  .t-dark .btn-tertiary {
    color: #fff;
    border-color: rgba(255, 255, 255, 0.2); }
    .t-dark .btn-tertiary:hover,
    .t-dark .btn-tertiary:focus {
      border-color: rgba(255, 255, 255, 0.4); }
    .t-dark .btn-tertiary:focus {
      box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.3); }
  .btn-tertiary:hover,
  .btn-tertiary:focus {
    border-color: rgba(255, 255, 255, 0.3); }
  .btn-tertiary:focus {
    box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.5); }

.btn-full {
  display: block;
  width: 100%; }

.btn-bare,
.btn-text-only {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer; }

.btn-text-only {
  color: #B22D26;
  font-size: 1em;
  line-height: inherit; }
  .btn-text-only:hover {
    text-decoration: underline; }
  .t-dark .btn-text-only {
    color: #fff;
    text-decoration: underline; }

.btn-s {
  padding-left: 0.375rem;
  padding-right: 0.375rem;
  font-size: 0.75rem; }

/*== Modules ==*/
/*================================================
 Global Header (gh)
==================================================
 Topmost page header found on most pages.
